import React, { useState } from 'react'

import { ReactComponent as Apps } from '../icons/app.svg'
import OverlayMenu from '../OverlayMenu'
import ThemeSelector from '../components/ThemeSelector'
import './AppBar.css'

class AppBar extends React.Component {

    constructor(props) {
        super(props)
        this.myRef = React.createRef()   // Create a ref object 
        this.state = {
            active: false,
        }
        this.toggleClass = this.toggleClass.bind(this);
    }



    toggleClass() {
        this.setState({ active: !this.state.active });
    }

    render() {

        return <div>
            <div className={this.state.active ? 'overlay-content-active' : "overlay-content-hide"}>
                <OverlayMenu toggleClass={this.toggleClass} />
            </div>
            <div className="header-hm">
                <button className="th-selector" onClick={this.toggleClass}>
                    <Apps className='icon' />
                </button>
                <ThemeSelector isLight={this.props.isLight}
                    toggleTheme={this.props.toggleTheme} />
            </div>
        </div>
    }
}

export default AppBar
