import React from 'react';
import './App.css';
import Home from './pages/Home'

import About from './pages/About'
import Skills from './pages/Skills'
import Contact from './pages/Contact'
import AppBar from './pages/components/AppBar'


class App extends React.Component {

  constructor(props) {
    super()
    this.state = {
      isLight: false,
    }
    this.toggleTheme = this.toggleTheme.bind(this);
  }

  toggleTheme() {
    this.setState({
      isLight: !this.state.isLight,
    })
  }


  render() {
    const isLight = this.state.isLight;

    return (

      <div className="app">
        <div className="app1" />

        <AppBar isLight={isLight}
          toggleTheme={this.toggleTheme} />

        <section id='home'>
          <Home />
        </section>

        <section id='about'>
          <About isLight={isLight} />
        </section>

        <section id='skills'>
          <Skills isLight={isLight} />
        </section>

        <section id='contact'>
          <Contact />
        </section>
      </div>
    );

  }
}

export default App;
