import React from 'react'
import './Skills.css'

import { Doughnut } from 'react-chartjs-2';



class Skills extends React.Component {

  render() {
    var style = getComputedStyle(document.documentElement);
    const s1 = style.getPropertyValue("--chart-1");
    const s2 = style.getPropertyValue('--chart-2');

    const data = {
      datasets: [{
        data: [80, 20],
        backgroundColor: [s1, s2],
        hoverBackgroundColor: [s1, s2],
        borderWidth: 1
      }],


      // These labels appear in the legend and in the tooltips when hovering different arcs
      labels: [
        'Health care',
        'Finance'
      ],

    };

    return (
      <div className="sk-1">

        <div className='sk-header'>
          <h1 className="sk-h1">#Skills </h1>
        </div>
        <div className="sk-content">
          <div className="sk-lc1">
            <span>#Java</span>
            <span>#Python</span>
            <span>#Javascript</span>
            <span>#HTML</span>
            <span>#CSS</span>
            <span>#Springboot</span>
            <span>#Angular</span>
            <span>#React</span>
            <span>#Docker</span>
            <span>#Maven</span>
            <span>#Gradle</span>
          </div>
          <div className='resume-sec'>
            <p> Looking for Hire ? </p>
            <a className="cv-download" href="https://drive.google.com/file/d/1jRINgms7dvyfcGfIpk9gpkru0fiXgNor/view?usp=sharing">
              download Resume
  </a>
          </div>


          <div className="sk-rc1">
            <h1>#Domain Experience </h1>
            <Doughnut data={data} />
          </div>
        </div>

        <div className="sk-footer" />
      </div>
    )

  }


}

export default Skills;