import React from 'react'
import './OverlayMenu.css'
import { ReactComponent as CloseIcon } from './icons/close.svg'
import { ReactComponent as SkillsIcon } from './icons/ui.svg'
import { ReactComponent as HomeIcon } from './icons/home.svg'
import { ReactComponent as AboutIcon } from './icons/About.svg'
import { ReactComponent as ContactIcon } from './icons/contact.svg'

class OverlayMenu extends React.Component {

    render() {

        return (
            <div className='overlay-menu-content'>

                <a className='oc-close' onClick={this.props.toggleClass}>
                    <CloseIcon className='icon' />
                </a>

                <div className="ovm-content-grid">

                    <a className='link ovm-b1' onClick={this.props.toggleClass} href='#home'>
                        <HomeIcon className="icon homeIcon">
                        </HomeIcon>
                        <h4> Home </h4>
                    </a>

                    <a className='link ovm-b2' onClick={this.props.toggleClass} href='#about'>
                        <AboutIcon className="icon aboutIcon">
                        </AboutIcon>
                        <h4> About </h4>
                    </a>

                    <a className='link ovm-b3' onClick={this.props.toggleClass} href='#skills'>
                        <SkillsIcon className="icon skillsIcon">
                        </SkillsIcon>
                        <h4> Skills </h4>
                    </a>

                    <a className='link ovm-b4' onClick={this.props.toggleClass} href='#contact'>
                        <ContactIcon className="icon conatctIcon">
                        </ContactIcon>
                        <h4> Contact </h4>
                    </a>
                    
                </div>
            </div>);

    }

}

export default OverlayMenu