import React from 'react'
import './ThemeSelector.css'
import { ReactComponent as Dark } from '../icons/bulb1.svg'
import { ReactComponent as Light } from '../icons/idea.svg'



class ThemeSelector extends React.Component {


    constructor(props) {
        super(props)
        this.toggleTheme = this.toggleTheme.bind(this);

        this.themes = {
            dark: {
                '--bg': '#000000',
                '--bgl': '#333',
                '--color': '#ffffff',
                '--white': 'white',
                '--chart-1': '#00FAB6',
                '--chart-2': '#FCF3CF',

                '--text-highlight': '#64ffda',
                '--body-highlight': '#64ffda',
                '--text-highlight-2': 'lime'

            },

            light: {
                '--bg': '#b8b08d',
                '--bgl': '#1d7874',
                '--color': '#283845',
                '--white': '#7d4f50',


                '--chart-1': '#ff5964',
                '--chart-2': '#FCF3CF',

                '--text-highlight': '#FCF3CF',
                '--body-highlight': '#1d7874',
                '--text-highlight-2': '#d62828'

            }



        }
    }

    applytheme = (obj) => {
        for (let [key, val] of Object.entries(obj)) {
            document.documentElement.style.setProperty(key, val);
        }

    }

    toggleTheme() {

        this.props.toggleTheme();

        var mode = !this.props.isLight ? 'light' : 'dark';
        this.applytheme(this.themes[mode]);
    }



    render() {

        return (
            <button className="th-selector" onClick={this.toggleTheme}>
                <Light className={this.props.isLight ? "icon light" : "icon hide"} />
                <Dark className={this.props.isLight ? "icon hide" : "icon dark"} />
            </button>
        );
    }
}

export default ThemeSelector